@font-face {
  font-family: 'fontello';
  src: url('icons/font/fontello.eot?17473298');
  src: url('icons/font/fontello.eot?17473298#iefix') format('embedded-opentype'),
       url('icons/font/fontello.woff2?17473298') format('woff2'),
       url('icons/font/fontello.woff?17473298') format('woff'),
       url('icons/font/fontello.ttf?17473298') format('truetype'),
       url('icons/font/fontello.svg?17473298#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
